
import React, { Component } from 'react';

class CurrencyCodeSelection extends Component {

  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(e) {
    this.props.onCurrencyChange(e.target.value);
  }

  render() {
    var options = this.props.currencies.map(currency =>
    <option value={currency.currencyCode}>{currency.countryName}({currency.currencyCode})</option>);

    return (
      <select className="form-control" value={this.props.selectedCurrencyCode} onChange={this.handleChange}>
        <option value="">Please select currency</option>
        {options}
      </select>
    );
  }
}
export default CurrencyCodeSelection;



