import React from 'react';
//import Label from 'react-bootstrap/lib/Label';
import CurrencyCodeSelection from './CurrencyCodeSelection';

class CurrencyForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

      selectedCurrencyCode: "",
      allCurrencyList: [],
      selectedCountryDetail: null
    };

    this.onCurrencyChange = this.onCurrencyChange.bind(this);
  }

  // Load currencies from server
  loadCurrenciesFromServer() {
    fetch('https://criclivescorer.com/CurrencyConverter/api/getAllCountryWithCurrencyCode', {
      method: "GET",
      headers: {
        "Accept": "application/json",
        "Origin": "github.com"
      }
    }).then((response) => response.json()).then((responseData) => {
      //  responseData.map(currency => console.log(currency.countryCode));
      this.setState({allCurrencyList: responseData.currencies});
    });
  }

  // Load currencies from server
  updateCurrencyForCountry(countryCode) {

    fetch('https://criclivescorer.com/CurrencyConverter/api/currencyDetailForCountry/' + countryCode, {
      method: "GET",
      headers: {
        "Accept": "application/json",
        "Origin": "github.com"
      }
    }).then((response) => response.json()).then((responseData) => {
      //  responseData.map(currency => console.log(currency.countryCode));
      this.setState({selectedCountryDetail: responseData});
    });
  }

  componentDidMount() {
    this.loadCurrenciesFromServer();
  }

  onCurrencyChange(currencyCode) {
    if(currencyCode === ""){
        this.setState({
          selectedCurrencyCode: "",
          selectedCountryDetail: null
        });

    }else{
    this.setState({selectedCurrencyCode: currencyCode});
    this.updateCurrencyForCountry(currencyCode);
  }
  }

  render() {

    return (
    <dev className="container">
      <p className="App-intro">
        Please select the country for the current currency rate for the US dollar value .
      </p>

      <form>
        <br/>

        <div className="row">
          <label className="col-xs-12 col-sm-4  control-label">Base Currency:</label>
          <label className="col-xs-6 col-sm-4  control-label"><i>USA Dollar</i></label>
        </div>

        <br/>

        <div className="row">
          <label   className="col-sm-4 col-xs-12 control-label">Select your currency:</label>

          <div className="col-sm-4">
            <CurrencyCodeSelection currencies={this.state.allCurrencyList} onCurrencyChange={this.onCurrencyChange} selectedCurrencyCode={this.state.selectedCurrencyCode}/>
          </div>

        </div>

        <br/>
        <br/>

        <div className="row">
          <label   className="col-sm-4 control-label">Current Currency Value:</label>

          <button type="button" className="col-sm-4 btn btn-primary">{
          this.state.selectedCountryDetail
            ? this.state.selectedCountryDetail.currencyValue
            : 'currency country not selected'
        } </button>




        </div>

      </form>
    </dev>);
  }
}

export default CurrencyForm;



