
import React, { Component } from 'react';
//import logo from './logo.svg';
import './App.css';
import Navbar from 'react-bootstrap/lib/Navbar';
import Nav from 'react-bootstrap/lib/Nav';
import NavItem from 'react-bootstrap/lib/NavItem';
//import NavDropdown from 'react-bootstrap/lib/NavDropdown';
//import MenuItem from 'react-bootstrap/lib/MenuItem';
//import Col from 'react-bootstrap/lib/Col';

class MainNavBar extends Component {

  constructor(props) {
    super(props);
    this.handleAboutLinkClick = this.handleAboutLinkClick.bind(this);
    this.handleCurrencyConverterLinkClick = this.handleCurrencyConverterLinkClick.bind(this);
  }

  handleAboutLinkClick(e) {
    this.props.onAboutLinkClicked();
  }

  handleCurrencyConverterLinkClick(e) {
    this.props.onCurrencyConverterLinkClicked();
  }

  render() {
    return (
      <Navbar>
          <Navbar.Header>
            <Navbar.Brand>
              <a  onClick={this.handleCurrencyConverterLinkClick} href="/">CurrencyConverter</a>
            </Navbar.Brand>
          </Navbar.Header>
          <Nav>
            <NavItem eventKey={1} href="#about" onClick={this.handleAboutLinkClick}>About</NavItem>
          </Nav>
        </Navbar>
    );

  }
}

export default MainNavBar;



